@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
}
body {
    overflow-y: hidden;
}
.nav_white .navigation li a{color:#ff784b}.nav_white .navigation li a:hover{color:#fff}.nav_white .navigation li a::before{background:#ff784b}.nav_red .navigation li a{color:#da463c}.nav_red .navigation li a:hover{color:#fff}.nav_red .navigation li a::before{background:#da463c}.nav_green .navigation li a{color:#50976a}.nav_green .navigation li a:hover{color:#fff}.nav_green .navigation li a::before{background:#50976a}.nav_blue .navigation li a{color:#3948b1}.nav_blue .navigation li a:hover{color:#fff}.nav_blue .navigation li a::before{background:#3948b1}.nav_black .navigation li a{color:#1d1d1f}.nav_black .navigation li a:hover{color:#fff}.nav_black .navigation li a::before{background:#1d1d1f}.nav{position:fixed;top:0;left:0;width:100%;padding:40px 100px;z-index:10000;display:flex;justify-content:center;align-items:center;transition:1s}.nav.sticky{background:#fff;padding:10px 100px;box-shadow:0 5px 20px rgba(0,0,0,0.05)}.nav.sticky .navigation li a{color:#222}.nav.sticky .navigation li a:hover{color:#ff0157}.nav.sticky .navigation li a::before{background:#222}.nav .navigation{position:relative;display:flex}.nav .navigation li{position:relative;list-style:none;margin:0 30px}.nav .navigation li a{text-decoration:none;font-weight:300}.nav .navigation li a::before{content:'';position:absolute;left:0;bottom:-10px;width:0px;height:5px;transition:0.5s ease-in-out;opacity:0}.nav .navigation li a:hover::before{width:100%;opacity:1}@media (max-width: 440px){.nav .navigation li{font-size:0.7em}}

